import React , { useRef } from 'react'
import emailjs from '@emailjs/browser';

import Swal from 'sweetalert2'

const Contact = () => {
  const form = useRef();
  const name = useRef();
  const email = useRef();
  const subject = useRef();
  function sendEmail(e) {
    e.preventDefault();
    if(name.current.value == ""){
      Swal.fire('name is empty')
      return
    }else if(email.current.value == ""){
      Swal.fire('email is empty')
      return
    }else {
      emailjs.sendForm('service_hshdeqy', 'template_5ast1wu', form.current, 'QRewmKub85EX_6kBl')
      .then((result) => {
        Swal.fire('thank I will text you soon')
          name.current.value = ""
          email.current.value = ""
          subject.current.value = ""
      }, (error) => {
          console.log(error.text);
      });
    }

  }
  return (
    <div id='contact' className='w-full py-[30vh] bg-[#0a192f] text-gray-300'>
        <div className='max-w-[900px] mx-auto px-3 md:px-8 flex flex-col justify-center  h-full'>
        <div className=' pb-8 pl-4'>
            <p className='text-4xl font-bold inline border-b-4 border-[#64fcda]'>
              Contact
            </p>
          </div>
            <form method='POST' className='flex flex-col mt-6' ref={form}  onSubmit={sendEmail}>
                <input type="text" name="name" ref={name} className=' bg-transparent outline-none p-3  border-b-2 border-b-gray-300 mb-6' placeholder='Name' />
                <input type="email" name="email" ref={email} className='bg-transparent outline-none p-3  border-b-2 border-b-gray-300 mb-6' placeholder='Email'/>
                <textarea name="subject" rows="8" ref={subject} className='bg-transparent outline-none p-3  border-b-2 border-b-gray-300 mb-6' placeholder='Subject'></textarea>
                <button className='text-white border-2  px-4 py-3 my-8 mx-auto flex items-center hover:bg-[#64fcda]  hover:border-[#64fcda] hover:text-[#0a192f]'>Let's Collaborate</button>
            </form>
        </div>
    </div>
  )
}

export default Contact