import { Link } from 'gatsby'
import React, { useState } from 'react'
import {FaBars,FaTimes,FaGithub,FaLinkedin,FaInstagram} from 'react-icons/fa';
import { BsFillPersonLinesFill } from 'react-icons/bs';

export default function Navbar() {
  const [nav, setNav] = useState(false);
  const handleClick = () => setNav(!nav);
  return (
    <div className='z-50 fixed w-full h-[80px] flex justify-between items-center px-4 bg-[#0a192f] text-gray-300'>
      <div>
        <h1 className='logo text-[#64fcda] font-bold text-4xl '>Zakaria.Dev-</h1>
      </div>
      {/* desktop menu */}
      <ul className='hidden md:flex  justify-between w-[500px]'>
        <li className="cursor-pointer text-xl hover:text-[#64fcda] duration-300">
          <Link to='/' >
            Home
          </Link>
        </li>
        <li className="cursor-pointer text-xl  hover:text-[#64fcda] duration-300">
          <Link to='#about' >
            About
          </Link>
        </li>
        <li className="cursor-pointer text-xl  hover:text-[#64fcda] duration-300">
          <Link to='#skills' >
            Skills
          </Link>
        </li>
        <li className="cursor-pointer text-xl hover:text-[#64fcda] duration-300">
          <Link to='#works' >
            Work
          </Link>
        </li>
        <li className="cursor-pointer text-xl hover:text-[#64fcda] duration-300">
          <Link to='#contact' >
            Contact
          </Link>
        </li>
      </ul>

      {/* Hamburger */}
      <button type="button" onClick={handleClick} className='md:hidden z-10'>
        {!nav ? <FaBars size={25}  /> : <FaTimes size={25} />}
      </button>

      {/* Mobile menu */}
      <ul
        className={
          !nav
            ? 'hidden'
            : 'absolute top-0 left-0 w-full h-screen bg-[#0a192f] flex flex-col justify-center items-center'
        }
      >
        <li className='py-6 text-4xl'>
          <Link onClick={handleClick} to='/'>
            Home
          </Link>
        </li>
        <li className='py-6 text-4xl'>
          {' '}
          <Link onClick={handleClick} to='#about' >
            About
          </Link>
        </li>
        <li className='py-6 text-4xl'>
          {' '}
          <Link onClick={handleClick} to='#skills' >
            Skills
          </Link>
        </li>
        <li className='py-6 text-4xl'>
          {' '}
          <Link onClick={handleClick} to='#work'>
            Work
          </Link>
        </li>
        <li className='py-6 text-4xl'>
          {' '}
          <Link onClick={handleClick} to='#contact' >
            Contact
          </Link>
        </li>
        <li className='py-6 flex justify-between items-center space-x-[30px]'>
          <Link to='https://www.linkedin.com/in/zakaria-bijaddigune-67373b231/' target='_blank'><FaLinkedin size={30} /></Link>
          <Link to='https://github.com/Zakaria0107' target="_blank"><FaGithub size={30} /></Link>
          <Link to='https://www.instagram.com/zakaria_bijaddigune/' target="_blank"><FaInstagram size={30} /></Link>
          <Link to='cv.pdf' target='_blank'><BsFillPersonLinesFill size={30} /></Link>
        </li>
      </ul>

      <div className='hidden lg:flex fixed flex-col top-[35%] left-0'>
        <ul>
          <li className='w-[160px] h-[60px] flex justify-between items-center ml-[-100px] hover:ml-[0px] duration-300 bg-[#0077B5]'>
            <a
              className='flex justify-between items-center w-full text-gray-300 px-2'
              href='https://www.linkedin.com/in/zakaria-bijaddigune-67373b231/' target='_blank'
            >
              Linkedin <FaLinkedin size={30} />
            </a>
          </li>
          <li className='w-[160px] h-[60px] flex justify-between items-center ml-[-100px] hover:ml-[0px] duration-300 bg-[#171515]'>
            <a
              className='flex justify-between items-center w-full text-gray-300 px-2'
              href='https://github.com/Zakaria0107' target="_blank"
            >
              Github <FaGithub size={30} />
            </a>
          </li>
          <li className='w-[160px] h-[60px] flex justify-between items-center ml-[-100px] hover:ml-[0px] duration-300 bg-[#E1306C]'>
            <a
              className='flex justify-between items-center w-full text-gray-300 px-2'
              href='https://www.instagram.com/zakaria_bijaddigune/' target="_blank"
            >
              Instagram <FaInstagram size={30} />
            </a>
          </li>
          <li className='w-[160px] h-[60px] flex justify-between items-center ml-[-100px] hover:ml-[0px] duration-300 bg-[#565f69]'>
            <a href="cv.pdf" target='_blank'
              className='flex justify-between items-center w-full text-gray-300 px-2'
            >
              Resume  <BsFillPersonLinesFill size={30} />
            </a>
          </li>
        </ul>
      </div>
    </div>

  )
}
